import { Document, Plugin, Block } from '@ali/4ever-cangjie';
import { headingGroupList } from '../conditions';
import { HeadingGroupConfig } from '../types';
import { HeadingGroupPlugin } from '@ali/4ever-bamboo';

const {
  utils,
} = HeadingGroupPlugin;

function defaultIgnore() {
  return false;
}

function createGroupNodes(config: HeadingGroupConfig): Plugin['groupNodes'] {
  const { level = 3, ignore = defaultIgnore, pass } = config;
  const groupConditions = headingGroupList.slice(0, level);

  return function groupNodes(range, ctx, controller, next) {
    const [start] = range;
    const { parent, groups } = ctx;
    const first = parent.nodes[start] as Block;
    const second = parent.nodes[start + 1];
    const parentGroup = groups[groups.length - 1];

    // 如果不是一级节点，或者是文档末尾，则不分组
    if (!Document.isDocument(parent) || !second || ignore(first)) {
      return next();
    }

    // 查找可用的 group 条件
    const $condition = groupConditions.find(({ type, when, until }) => {
      return when(first) && !utils.isGroupFrom(parentGroup, type);
    });

    // group 节点
    if ($condition) {
      const { until, type } = $condition;
      return utils.group(range, parent, type, until, pass);
    }

    return next();
  };
}

export default createGroupNodes;
